@import url(https://fonts.googleapis.com/css2?family=Raleway);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: rgb(15,15,15);
  color: white;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;

}

.scroll
{
  position: fixed;
    transition: 0.2s;
    width: 1%;
    
    top: 1vh;
    /* left: 98.5%; */
    right: 0.5%;

    transform: rotate(180deg);
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.transitionBody{
    position: fixed;
    top: 100%;
    background-color: rgb(5,5,5);
    width: 100vw;
    height: 100vh;
    transition: 0.5s ease-in-out;
    display: block; /*block when in transition*/
    opacity: 1; /*1 when in transition I guess*/
    z-index: 100000;
}
.navbar{
    position: fixed;

    list-style: none;
    padding: 0;
    margin: 0;
    margin-top: 1%;
    margin-left: 10%;

    display: flex;
    flex-direction: row;
    align-items: center;

    font-weight: 700;
    font-size: 110%;
    letter-spacing: 3;

    transition: 0.2s;
    opacity: 1;
}

.navitem{
    margin-left: 1.5%;
    margin-right: 1.5%;
    transition: 0.3s;
    cursor: pointer;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
.navitem:hover{
    opacity: 0.6;
}

.navrocket{
    height: 2.25vw;
    rotate: 90deg;
    transition: 0.2s ease-out;
}
.navrocket:hover{
    transform: rotateZ(-90deg);
}
.Footer{
    background-color:rgb(15,15,15);
    z-index: 10;
    font-size: 125%;
}

.wrapper{
    margin: 1%;
}

.name{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin-bottom: 0.5%;
}

.datenschutz{
    border: solid white 2px;
    background-color: rgb(15,15,15);
    padding: 5px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    cursor: pointer;
    transition: .5s ease;
}

.datenschutz:hover{
    background-color: white;
    color: rgb(15,15,15);
}
.indexBg{
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    transform: translateX(-50%) translateY(-50%);
    background: url(/tech/static/media/background.7a1d47fb.mp4) no-repeat;
    background-size: cover;
}

.icover{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 10%;
    margin-top: 30%;
    margin-bottom: 10%;
}

.ititle{
    font-size: 2rem;
    letter-spacing: 0.25rem;
    margin-bottom: 1%;
}

.iButton{
    text-align: center;
    border: solid white 2px;
    background-color: rgba(15,15,15,0);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    cursor: pointer;
    box-sizing: border-box;
    transition: .5s ease;

    padding: 1.2rem 2.2rem;
	padding-right: 1.1rem;
	font-size: 1.4rem;
	color: var(--inv);
    letter-spacing: 1.1rem;
    z-index: 1;
}
.ioverlay {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: white;
    width: 10%;
    height: 100%;
    transition: .5s ease;
    z-index: 0;
}
  
  .icontainer:hover .ioverlay, .icontainer:hover .iButton{
    width: 100%;
    color: black;
}

.icontainer {
    display: flex;
    position: relative;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}


/*Subcomponent*/
.iconcover{
    width: 100%;
    height: 120vh;
    
    z-index: -1;
    background-size: cover;

    display: flex;
    flex-direction: column;
}
.fire{background-image: url(/tech/static/media/fire.a7f9bcba.jpg);}
.astro{background-image: url(/tech/static/media/astro.00f2539b.jpg);}
.projects{background-image: url(/tech/static/media/code.b5608b71.png);}

.icontitle{
    display: flex;
    color: white;
    flex: 0.5 1;

}
.icontitleC{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-size: 3rem;
    letter-spacing: 0.33rem;
}

.icontitleC1{ /*Photography Position*/
    margin-top: auto;
    margin-left: auto;
    margin-right: 10%;
    margin-bottom: 1%;
}
.icontitleC2{ /*ASTROPhotography Position*/
    margin-top: auto;
    margin-right: auto;
    margin-left: 10%;
    margin-bottom: 1%;
}
/*Text Box*/
.iconmain{
    display: flex;
    position: relative;
    flex: 0.5 1;
}

.iconmainC{
    color: white;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    cursor: pointer;
    transition: .5s ease;
    border: solid white 2px;
    box-sizing: border-box;

    font-size: 1.4rem;
    text-align: center;
    padding: 1.2rem 2.2rem;
    padding-right: 1.1rem;
    letter-spacing: 1.1rem;
    z-index: 1;
}
.contentcontainer{
    position: relative;
    display: flex;
}

.contentcontainer1{ /*Photography Position*/
    margin-left: auto;
    margin-bottom: auto;
    margin-right: 10%;
}
.contentcontainer2{ /*Astrophotography position*/
    margin-right: auto;
    margin-bottom: auto;
    margin-left: 10%;
}
.iconmainC:hover{
    color:black
}

.contentcontainer:hover .ioverlay{
    width: 100%;
}
